.hover-border-animate {
  position: relative;
  background: oklch(0.08 0.01 220);
}
.hover-border-animate::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: inherit;
  padding: 2px;
  background: linear-gradient(45deg, oklch(0.65 0.25 258), oklch(0.75 0.12 195), oklch(0.70 0.18 155), oklch(0.65 0.25 258));
  background-size: 300% 300%;
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  animation: gradient-shift 3s ease infinite;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.hover-border-animate:hover::before {
  opacity: 1;
}
.reveal-on-scroll {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.6s ease, transform 0.6s ease;
}
.reveal-on-scroll.revealed {
  opacity: 1;
  transform: translateY(0);
}
.float-animate {
  animation: float 6s ease-in-out infinite;
}
.float-animate-delayed {
  animation: float-delayed 8s ease-in-out infinite;
}
.blog-content {
  & > * + * {
    margin-top: 1.5rem;
  }
}
.blog-content h1 {
  font-size: 1.875rem;
  font-weight: 700;
  color: white;
  margin-bottom: 1.5rem;
  margin-top: 3rem;
}
.blog-content h1:first-child {
  margin-top: 0;
}
.blog-content h2 {
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
  margin-bottom: 1rem;
  margin-top: 2.5rem;
}
.blog-content h3 {
  font-size: 1.25rem;
  font-weight: 600;
  color: white;
  margin-bottom: 0.75rem;
  margin-top: 2rem;
}
.blog-content h4 {
  font-size: 1.125rem;
  font-weight: 600;
  color: white;
  margin-bottom: 0.5rem;
  margin-top: 1.5rem;
}
.blog-content p {
  color: rgb(209 213 219);
  line-height: 1.75;
  margin-bottom: 1rem;
}
.blog-content ul,
.blog-content ol {
  color: rgb(209 213 219);
  margin-bottom: 1rem;
  padding-left: 1.5rem;
}
.blog-content li {
  margin-bottom: 0.5rem;
}
.blog-content blockquote {
  border-left: 4px solid var(--color-primary-400);
  background: oklch(0.55 0.30 258 / 0.05);
  border-radius: 0 0.5rem 0.5rem 0;
  padding-left: 1.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  font-style: italic;
  color: rgb(209 213 219);
}
.blog-content img {
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1);
  margin-left: auto;
  margin-right: auto;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  max-width: 100%;
  height: auto;
}
.blog-content hr {
  border-color: rgb(255 255 255 / 0.2);
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.line-clamp-2 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
.blog-content .inline-code,
.blog-content code:not(pre code) {
  position: relative;
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
  background: linear-gradient(to right, oklch(0.55 0.30 258 / 0.15), oklch(0.65 0.25 258 / 0.15));
  color: var(--color-primary-300);
  border-radius: 0.375rem;
  border: 1px solid oklch(0.55 0.30 258 / 0.25);
  font-family: var(--default-mono-font-family);
  font-weight: 500;
  transition: all 0.2s;
  backdrop-filter: blur(4px);
}
.blog-content .inline-code:hover,
.blog-content code:not(pre code):hover {
  background: oklch(0.55 0.30 258 / 0.2);
  border-color: oklch(0.65 0.25 258 / 0.4);
}
.blog-content pre {
  margin-top: 2rem;
  margin-bottom: 2rem;
  overflow: hidden;
  border-radius: 0.5rem;
  box-shadow: 0 20px 25px -5px rgb(0 0 0 / 0.1);
}
.blog-content pre code {
  background: transparent;
  border: 0;
  padding: 0;
  color: inherit;
  font-family: var(--default-mono-font-family);
}
.blog-content .code-block-wrapper {
  position: relative;
  margin-top: 2rem;
  margin-bottom: 2rem;
  overflow: hidden;
  border-radius: 0.5rem;
}
.blog-content .hljs {
  backdrop-filter: blur(12px);
  border: 1px solid rgb(255 255 255 / 0.1);
  background: rgb(0 0 0 / 0.4) !important;
  border-radius: 0.75rem;
}
.blog-content pre::-webkit-scrollbar {
  height: 8px;
  width: 8px;
}
.blog-content pre::-webkit-scrollbar-track {
  background: rgb(255 255 255 / 0.05);
  border-radius: 4px;
}
.blog-content pre::-webkit-scrollbar-thumb {
  background: rgb(168 85 247 / 0.3);
  border-radius: 4px;
}
.blog-content pre::-webkit-scrollbar-thumb:hover {
  background: rgb(168 85 247 / 0.5);
}
.blog-content .hljs-ln-line.highlighted {
  background-color: rgb(168 85 247 / 0.1) !important;
  border-left: 3px solid #a855f7;
  padding-left: 1rem !important;
}
.animate-shimmer {
  animation: shimmer 3s linear infinite;
}
.animation-delay-1000 {
  animation-delay: 1s;
}
